<template>
  <b-container fluid="">
    <b-form>
      <b-row class="mt-2">
        <b-col sm="2" offset="2">
          <b-input v-model="newIme" class="mb-2 mr-sm-2 mb-sm-0" placeholder="ime"></b-input>
        </b-col>
        <b-col sm="2">
          <b-input v-model="newPrezime" class="mb-2 mr-sm-2 mb-sm-0" placeholder="prezime"></b-input>
        </b-col>
        <b-col sm="2">
          <b-input v-model="newUlica" class="mb-2 mr-sm-2 mb-sm-0" placeholder="ulica"></b-input>
        </b-col>
        <b-col sm="1">
          <b-button variant="primary" size="lg" @click="save">Save</b-button>
        </b-col>
      </b-row>
    </b-form>
  </b-container>
</template>

<script>
import { mapActions } from 'vuex';
const Joi = require('joi');

const editSchema = Joi.object().keys({
  ime: Joi.string().trim().min(2).max(50).required(),
  prezime: Joi.string().trim().min(2).max(50).required(),
  ulica: Joi.string().trim().min(2).max(50).required()
});

export default {
  name: "EditMember",
  props: {
    ime: {
      type: String,
      default: ''
    },
    prezime: {
      type: String,
      default: ''
    },
    ulica: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      newIme: '',
      newPrezime: '',
      newUlica:' '
    }
  },
  mounted: function () {
    this.newIme = this.ime;
    this.newPrezime = this.prezime;
    this.newUlica = this.ulica;
  },
  methods: {
    ...mapActions(['new_member', 'change_member']),

    save: function() {
      let {error} = editSchema.validate({ime: this.newIme, prezime: this.newPrezime, ulica: this.newUlica});

      if(error) {
        alert(error.details[0].message);
      }else {
        const msg = JSON.stringify({ime: this.newIme, prezime: this.newPrezime, ulica: this.newUlica});

        if (!this.$route.params.id)
          this.new_member(msg);
        else
          this.change_member({id: this.$route.params.id, msg: msg});

        this.newIme = '';
        this.newPrezime = '';
        this.newUlica = '';
      }
    }
  }
}
</script>

